.cartcontrol{font-size:0}.cartcontrol .cart-decrease,.cartcontrol .cart-add{font-size:24px;line-height:24px;display:inline-block;padding:6px;color:#00a0dc}.cartcontrol .cart-decrease.move-transition{-webkit-transition:all 0.4s linear;transition:all 0.4s linear;opacity:1;-webkit-transform:translate3d(0, 0, 0) rotate(0);transform:translate3d(0, 0, 0) rotate(0)}.cartcontrol .cart-decrease.move-enter,.cartcontrol .cart-decrease.move-leave{opacity:0;-webkit-transform:translate3d(24px, 0, 0) rotate(180deg);transform:translate3d(24px, 0, 0) rotate(180deg)}.cartcontrol .cart-count{display:inline-block;vertical-align:top;padding-top:6px;width:12px;text-align:center;font-size:10px;line-height:24px;color:#93999f}



